<template>
  <div>
    <i class="el-icon-circle-plus-outline"  @click="dialogFormVisible = true"></i>
    <el-dialog
      title="/"
      :visible.sync="dialogFormVisible"
      style="max-width: 8000px"
      @close="clear">
          <el-form v-model="form" style="text-align: left;" ref="dataForm">
            <el-row :gutter="40">
              <el-col :span="15">
            <el-form-item prop="id" style="height: 0">
              <el-input type="hidden" v-model="form.id" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="name">
              <el-input v-model="form.name" autocomplete="off" ></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="uname">
              <el-input v-model="form.uname" autocomplete="off" ></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="idnum">
              <el-input v-model="form.idnum" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="sex">
              <el-select v-model="form.sex" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="natived">
              <el-input v-model="form.natived" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="date">
              <div class="block">
                <el-date-picker
                  v-model="form.birthdate"
                  type="date"
                  placeholder="">
                </el-date-picker>
              </div>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="height">
              <el-input v-model="form.height" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="photo">
              <el-input v-model="form.photo" autocomplete="off"></el-input>
              <img-upload @onUpload="uploadImg" ref="imgUpload"></img-upload>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="naplace">
              <el-input v-model="form.naplace" autocomplete="off" ></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="religion">
              <el-input v-model="form.religion" autocomplete="off" ></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="birplace">
              <el-input v-model="form.birplace" autocomplete="off" ></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="15">
          <el-form-item label="" :label-width="formLabelWidth" prop="address1" >
            <el-input v-model="form.address1" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="edudegree">
            <el-select v-model="form.edudegree" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="emplace" >
            <el-input v-model="form.emplace" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="whenincity">
            <div class="block">
              <el-date-picker
                v-model="form.whenincity"
                type="date"
                placeholder="">
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="whereincity" >
            <el-input v-model="form.whereincity" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="whenin">
            <div class="block">
              <el-date-picker
                v-model="form.whenin"
                type="date"
                placeholder="">
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="wherein" >
            <el-input v-model="form.wherein" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="occupation" >
            <el-input v-model="form.occupation" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="arstatus">
            <el-select v-model="form.arstatus" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="marstatus">
            <el-select v-model="form.marstatus" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth" prop="reholder">
            <el-select v-model="form.reholder" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>


      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import ImgUpload from './ImgUpload'
  export default {
    name: 'EditForm',
    components: {ImgUpload},
    data () {
      return {
        dialogFormVisible: false,
        form: {
          id: '',
          name: '',
          uname: '',
          naplace: '',
          birplace: '',
          address1: '',
          edudegree: '',
          emplace: '',
          whenincity: '',
          whereincity: '',
          whenin: '',
          wherein: '',
          occupation: '',
          arstatus: '',
          marstatus: '',
          height: '',
          religion: '',
          birthdate: '',
          natived: '',
          sex: '',
          reholder: '',
          photo: '',
          idnum: ''
        },
        formLabelWidth: '160px'
      }
    },
    methods: {
      clear () {
        this.form = {
          id: '',
          name: '',
          uname: '',
          naplace: '',
          birplace: '',
          address1: '',
          edudegree: '',
          emplace: '',
          whenincity: '',
          whereincity: '',
          whenin: '',
          wherein: '',
          occupation: '',
          arstatus: '',
          marstatus: '',
          height: '',
          religion: '',
          birthdate: '',
          natived: '',
          sex: '',
          reholder: '',
          photo: '',
          idnum: ''
        }
      },
      uploadImg () {
        this.form.photo = this.$refs.imgUpload.url
      },
      onSubmit () {
        this.$axios
          .post('/residents', {
            id: this.form.id,
            name: this.form.name,
            uname: this.form.uname,
            naplace: this.form.naplace,
            birplace: this.form.birplace,
            address1: this.form.address1,
            edudegree: this.form.edudegree,
            emplace: this.form.emplace,
            whenincity: this.form.whenincity,
            whereincity: this.form.whereincity,
            whenin: this.form.whenin,
            wherein: this.form.wherein,
            occupation: this.form.occupation,
            arstatus: this.form.arstatus,
            marstatus: this.form.marstatus,
            height: this.form.height,
            religion: this.form.religion,
            birthdate: this.form.birthdate,
            natived: this.form.natived,
            sex: this.form.sex,
            reholder: this.form.reholder,
            photo: this.form.photo,
            idnum: this.form.idnum
          }).then(resp => {
          if (resp && resp.status === 200) {
            this.dialogFormVisible = false
            this.$emit('onSubmit')
          }
        })
      }
    }
  }
</script>

<style scoped>
  .el-icon-circle-plus-outline {
    margin: 50px 0 0 20px;
    font-size: 90px;
    float: left;
    cursor: pointer;
  }
  .el-input {
    autosize: true
  }
</style>





